@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@300;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Muli:wght@300;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap%22");

/* *{
  margin: 0;
