body {
  margin: 0;
  font-family: 'Muli', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3{
  font-family: 'Nunito';
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

html {
  height: 100%;
}
.brand-font {
  font-family: 'Permanent Marker', cursive;
}
body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin: 0;
  font-family: 'Muli', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
}

footer {
  flex-shrink: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, button {
  font-family: 'Nunito';
  font-weight: 800 !important;
}

h2 {
  font-size: 3rem !important;
}

input {
  color: #222;
}

.brand-font {
  font-family: 'Permanent Marker', cursive;
}

.brand-footer {
  padding-left: 10px;
  font-size: 2.5rem;
  display: flex;
  align-items: flex-end;
}

/* SIGNIN */

.signin-body {
  background: #fff;
  width: 600px !important;
  margin: auto;
  padding: 30px;
  position: relative;
  margin: 50px 0px 0px 0px;
}

.signin-logo {
  text-align: center;
  margin-bottom: 50px;
}

.signin-logo img {
  width: 80px;
}

.psignin {
  color: #70747f;
}

/* BUTTONS */

.button-signup {
  text-align: center;
  color: #fff;
  padding: 15px 25px;
  border: none;
  height: 60px;
  border-radius: 30px;
  background-color: #ffc85b;
  transition-duration: .5s;
}

.button-signup:hover {
  background-color: #45b3df;
  transform: rotate(5deg);
}

.button-signup:focus {
  outline: none;
}

.button-avatar {
  text-align: center;
  color: #000;
  padding: 15px 25px;
  border: none;
  height: 60px;
  border-radius: 15px;
  background-color: #fff;
  transition-duration: .5s;
}

.button-avatar:hover {
  transform: rotate(5deg);
}

/* CARDS */

.avatars-body {
  background: #fff;
  margin: auto;
  padding: 30px;
  position: relative;
  margin: 70px 0px;
}

.card-group {
  display: flex;
}

.card-body {
  text-align: center;
  display: flex;
  flex-direction: column;
  border: 5px solid #fff;
  border-radius: 30px;
  transition-duration: .5s;
}

.card-body:hover {
  transform: scale(1.05);
}

.card-img {
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card1 {
  background: #3F88DB;
}

.card2 {
  background: #EA9BB7;
}

.card3 {
  background: #57B3DF;
}

.card4 {
  background: #ffc85b;
}

.card-body img {
  width: 80%;
}

.card-footer {
  border: none;
}

.card {
  border: none !important;
  margin: 5px;
  border-radius: 30px;
}

/* MODALS */

.btn-outline-danger {
  color: #57B3DF !important;
  border-color: #57B3DF !important;
}

.btn-outline-danger:hover {
  color: #fff !important;
  background-color: #57B3DF !important;
  transform: scale(1.2) !important;
}

.btn-outline-secondary:hover {
  color: #6c757d !important;
  background-color: #E5E5E5 !important;
}

.modal-body {
  text-align: center;
}

.modal-footer {
  justify-content: center !important;
}

@media screen and (max-width: 768px) {
  .card-body img {
    width: 40%;
  }
}

/* FOOTER */

footer p {
  margin: 0px;
}

.page-footer {
  background-color: #EFB7CC;
  color: #fff;
  width:100%;
  padding-top:50px;
}

.page-footer a {
  text-decoration: none;
  color: #fff;
}

.page-footer img {
  width: 150px;
}

.footer-copyright {
  margin-top: 10px;
}

#img-footer-xs {
  display: none;
}

#img-footer-lg {
  width: 50px;
  margin-left: 15px;
}

.love {
  width: 15px !important;
  padding-bottom: 5px;
}

.social {
  width: 30px !important;
  margin: 0px 15px;
}

.social:hover {
  transform: scale(1.2);
}

.list-unstyled a:hover {
  color: #fff;
  text-decoration: none;
}

hr {
  margin-top: 1rem;
  margin-bottom: 0px !important;
}

@media screen and (max-width: 768px) {
  .brand-footer-sm {
    justify-content: center;
    padding-bottom: 10px;
  }
  .social-media {
    justify-content: center;
  }
}
.PortraitDino {
    border: 1px solid black;
    width: 25%;
    height: 240px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    background-color: white;
    border: none;
}
.PortraitContainer{
    width: 100%;
}

.QuizzContainer {
    width: 50%;
    height: 100vh;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    display: flex;
    flex-direction: column;
    border: 1px solid #dfe6e9;
    margin-left: 25%;;
    align-items: center;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 5px 5px 5px grey;
    margin-top: 50px;
    margin-bottom: 50px;
    opacity: 0.8;
}

.Question {
    width: 50%;
    height: 100px;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    border: none;
    margin-top: 100px;
    padding:15px;
    background-color: #88D4F2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.AnswersContainer {
    display: flex;
    flex-direction: column;
}

.AnswerBlock{
    display: flex;
    margin-top: 50px;
}
.Answer {
    width: 250px;
    margin: 15px;
    height: 70px;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    border: none;
    padding: 15px;
    background-color: #F49CB6;
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.Answer:hover {
    cursor: pointer;
    background-color: #ADCA52;
    transition: 0.5ms;
}


.Explanation {
    margin-top: 50px;
    width: 75%;
    height: 200px;
    border: none;
    background-color: #FDC563;
    padding: 15px;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    text-overflow: ellipsis;
    margin-bottom: 15px;
}

.Quizz-button{
    border-radius: 15px;
    border: none;
    padding: 10px;
}

.Quizz-button:hover {
    background-color: grey;
}

@media screen and (max-width: 834px){
    
    .QuizzContainer{
        width: 96%;
        height: auto;
        height: initial;
        margin-left: 2%;
    }

    .PortraitDino{
        height: 73px;
        width: 10%;
    }

    .PortraitDino img{
        width: 70px;
    }

    .Question{
        height: 70px;
        width: 250px;
    }

    .Answer{
        height: 50px;
        width: 200px;
        cursor: pointer;
    }
}

@media screen and (max-width: 767px){

    .PortraitDino{
        height: 73px;
        width: 20%;
    }

    .AnswerBlock{
        flex-direction: column;
        margin-top: 0;
    }
}
.Info{
  display: flex;
  flex-direction: column;
  text-align: center;
  border-radius: 1em;
  background-color: #fdfaf5;
  margin: 40px 50px 40px 50px;
}

.Info .ContainerInfoTitle{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding-bottom: 0px;
}

.Info .InfoTitle{
  color: #13265a;
  font-weight: 900;
  background-color:#ffc85c;
  width: 100%;
  height: 100px;
  line-height: 100px;
  margin-bottom: 0px;
  border-radius: 10px 10px 0em 0em;
  -webkit-border-radius: 10px 10px 0em 0em;
  -moz-border-radius: 10px 10px 0em 0em;
  -ms-border-radius: 10px 10px 0em 0em;
  -o-border-radius: 10px 10px 0em 0em;
}

.Info .InfoBody{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 50px;
  border: 2px dashed #7b7e89;
  border-radius: 0px 0px 10px 10px;
  border-top: none;
}

.Info .ContainerDesease{
  display: flex;
  align-items: center;
}

.Info .InfoNameDesease, .InfoMail, .InfoPuzzle{
  font-weight: 700;
  color: #13265a;
}

.Info .InfoPuzzle{
  margin-top: 40px;
  text-decoration: underline;
  transition-duration: .2s;
}
.Info .InfoPuzzle:hover{
  color: #87D3F1;
}

.Info .InfoMail{
  margin-top: 50px;
}

.Info .InfoMediaBody{
  width: 600px;
  height: 300px;
  border: none;
  box-shadow: none;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}
.Info .ContainerText{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 20px;
}

.Info .ContainerContentText{
  width: 75%;
}

.Info .ContainerTextElement{
  color:#7b7e89;
  text-align: left;
  text-align: initial;
  font-size: 1rem;
}

.Info .ContainerForm{
  margin-top: 20px;
}

.Info .FormEmailElement{
  width: 30%;
  padding: 10px 20px ;
  margin-right: 20px;
  border: 1px solid #b2bec3;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.Info .FormSubmitElement{
  padding: 15px 25px;
  font-weight: 700;
  background-color: #87D3F1;
  cursor: pointer;
  border: none;
  border-radius: 30px;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  -ms-border-radius: 30px;
  -o-border-radius: 30px;
  transition-duration: .5s;
}

.Info .FormSubmitElement:hover{
  background-color: #ffc85c;
  transform: rotate(5deg);
  -webkit-transform: rotate(5deg);
  -moz-transform: rotate(5deg);
  -ms-transform: rotate(5deg);
  -o-transform: rotate(5deg);
}

